import React from 'react';
import ReactDOM from 'react-dom';

class Slider extends React.Component {
  constructor(props) {
    super(props);
    this.state = { data: [] };
  }

  componentDidMount() {
    let data = [
      {
        title: "My Image 1",
        url: "www.facebook.com",
        img: "https://i.imgur.com/jrIXCSJ.jpg",
        caption: "Something important text"
      },
      {
        title: "My Image 2",
        url: "www.reddit.com",
        img: "https://i.imgur.com/wY3MMsQ.jpg",
        caption: "Something important text"
      },
      {
        title: "My Image 3",
        url: "www.google.com",
        img: "https://i.imgur.com/Vyfmb20.jpg",
        caption: "Something important text"
      }
    ];

    setTimeout(function(){
      this.setState({data: data});
    }.bind(this), 5000);
  }

  componentDidUpdate() {
    $('.flexslider').flexslider();
  }

  render() {
    let data = this.state.data;
    let slides = data.map((slide, index)=> {
      return (<Slide key={index} title={slide.title} url={slide.url} img={slide.img} caption={slide.caption}/>)
    })
    return (
      <ul className="slides">
        {slides}
      </ul>
    )
  }
}

const Slide = ({title, url, img, caption}) => {
  return (
    <li>
      <a href={url}><img src={img} /></a>
      <p className="flex-caption">{caption}</p>
    </li>
  )
}

ReactDOM.render(
  <Slider />,
  document.getElementById('flexslider')
);


function resizePart() {
    var senderId = GetUrlKeyValue("SenderId");
    var parent = window.parent;

    if (senderId && parent) {
        var height = $("#flexslider").height();
        var width = 600;

        var message = String.format("<message senderId={0}>resize({1},{2})</message>",
            senderId, width, height);
        parent.postMessage(message, "*");
    }
}

resizePart();



